.Game {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background-color: black;
    color: white;
    margin: 0 auto;
    overflow: visible;
}

#tetris {
    width: 100%;
    text-align: center;
    color: white;
    font-size: 6rem;
    margin-top: 10%;
}

.button-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10%;
}
.button-list button {
    position: relative;
    width: max-content;
    background-color: transparent;
    border: none;
    font-family: 'WindowsRegular';
    color: white;
    padding: 1rem;
    font-size: 2rem;
    letter-spacing: 3px;
}
.button-list button span {
    position: absolute;
    left: -10px;
    margin-right: 1rem;
    opacity: 0;
}
.button-list button:hover span {
    opacity: 1;
}
