.loading {
    position: absolute;
    display:flex;
    flex-direction: column;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 30%;
    height: 30%;
}

.loading>h1{
    font-size: 32pt;
    color: white;
    text-align: center;
}
.loading-bar {
    display: flex;
    align-items: center;
    width: 100%;
    height: 30px;
    background-color: black;
    gap:3px;
    border: 2px solid rgb(0, 61, 0);
    padding: 2px 2px;
  }
  
  .loading-block {
    width: 10%;
    height: 100%;
    background-color: green;
    opacity: 0;
    animation: fillBlock 1s both;
  }
  
  .loading-block:nth-child(1) {
    animation-delay: 0s;
  }
  
  .loading-block:nth-child(2) {
    animation-delay: 0.3s;
  }
  
  .loading-block:nth-child(3) {
    animation-delay: 0.6s;
  }
  
  .loading-block:nth-child(4) {
    animation-delay: 0.9s;
  }
  .loading-block:nth-child(5) {
    animation-delay: 1.2s;
  }
  .loading-block:nth-child(6) {
    animation-delay: 1.5s;
  }
  .loading-block:nth-child(7) {
    animation-delay: 1.8s;
  }
  .loading-block:nth-child(8) {
    animation-delay: 2.1s;
  }
  .loading-block:nth-child(9) {
    animation-delay: 2.4s;
  }
  .loading-block:nth-child(10) {
    animation-delay: 2.7s;
  }
  
  @keyframes fillBlock {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }