.footer{
    background-color: var(--color-gray);
    position: absolute;
    display: flex;
    width: 100%;
    bottom: 0px;
    height: 50px;
    box-shadow: 1px black;
    border-top: 3px solid white;
}


/* 시작버튼 */
.window {
    background-color: var(--color-gray);
    cursor: pointer;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: max-content;
    height: max-content;
    padding: 2px 4px;
    margin: auto 0;
    margin-left: 10px;
    white-space: nowrap;
    box-shadow: 2.5px 2.5px 0 #6a6a61;
    border-top: 2px solid white;
    border-left: 2px solid white;
}
.window98 {
    width: 25px;
    height: 25px;
    margin-right: 5%;
}
.start {
    font-family: 'WindowsRegular';
    padding-top:1px;
    text-align: center;
    font-size: 0.8rem;
    font-weight: bold;
    text-align: center;
    letter-spacing: 2px;
}
.start-button-highlight{
    display:block;
    width:4px;
    height:1px;
    background-color: white;
}




/* 시작버튼 옆 아이콘 모음 및 가상 bar */
.tools {
    display: flex;
    width: 20%;
    align-items: center;    
}
.vertical-line {
    width: 2px;
    height: 29px;
    margin: auto 7px;
    background-color: var(--color-gray-shadow);
    border-right: 2px solid white;
}
.vertical-bar {
    width: 2px;
    height: 20px;
    background-color: var(--color-gray);
    border-top: 2px solid white;
    border-left: 1px solid white;
    border-right: 2px solid gray;
    border-bottom: 2px solid gray;
    border-radius: 2px;
}
.shortcut-icon-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: auto;
}
.icon-button {
    background-color: transparent;
    max-width: fit-content;
    width: 50px;
    height: 100%;
    border: none;
    cursor: pointer;
    padding: 0;
}

.footer-icon {
    position: absolute;
    bottom: 3px;
    width: 50px;
}


/* 시간표시기 */
.time-container {
    border: 2px solid gray; 
    width: 150px;
    height: 40px;
    position: absolute;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    bottom: 5px;
    right: 5px;
}
.icon-container2 {
    display: flex;
    justify-content: space-around;
}
.time {
    font-size: 1.2rem;
    letter-spacing: 2px;
}