.desktop{
    width: 100vw;
    height: 100vh;
}

.desktop span{
    text-align: center;
}
.desktop-icons {
    display: flex;
    flex-direction: column;
}
.desktop-button {
    display: inline-block;
    background-color: transparent;
    cursor: pointer;
    border: none;
    width: 90px;
    height: 90px;
    padding: 10px;
    margin: 1rem;
}
/* 바탕화면 아이콘 밑 글자색 변경 */
.desktop-button > span {
    font-family: 'WindowsRegular';
    color: white;
    font-size: 1rem;
    letter-spacing: 2px;
    white-space: nowrap;
}

/* Desktop TETRIS Icon */
.desktop-icon {
    width: 100%;
    margin-bottom: -1rem;
}

.desktop-tetris{
    width:100%;

}